@keyframes pulse-small {
  0% {
    transform: translate(-50%, -50%) scale(0.8);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -50%) scale(1.7);
    opacity: 0;
  }
}
@keyframes pulse-big {
  0% {
    transform: translate(-50%, -50%) scale(0.8);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -50%) scale(2.7);
    opacity: 0;
  }
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "Inter", sans-serif;
  background-color: #ffffff;
  color: #070c3a;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.2;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

.bottom-overflow {
  overflow: hidden;
}

main {
  position: relative;
  z-index: 1;
}

a {
  color: #ffffff;
  text-decoration: none;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

[hidden] {
  display: none !important;
}

/* Mobile-menu */
[data-state=mobile-menu] .header__menu-wrapper {
  transform: translateX(0);
}
[data-state=mobile-menu] .overlay {
  opacity: 1;
  pointer-events: auto;
}

/* Adv mark article */
.token-mark-block {
  display: none;
  position: relative;
  z-index: 20;
  max-width: 1288px;
  width: 100%;
  padding: 0 20px;
  margin: 30px auto 0;
  box-sizing: border-box;
}

.article-mark:hover + .token-info {
  opacity: 1;
  pointer-events: auto;
}

.token-info {
  position: absolute;
  left: 0;
  bottom: 100%;
  width: 270px;
  border: 2px solid #ff9c34;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(11px);
  border-radius: 2px;
  padding: 20px 10px;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease;
}

.token-info-item {
  display: block;
  color: #ffffff;
  font-size: 12px;
  line-height: 1.2;
}
.token-info-item a {
  color: #ffffff;
  text-decoration: underline;
}
.token-info-item a:hover {
  text-decoration: none;
}

/* Оверлей для меню */
.overlay {
  position: fixed;
  inset: 0;
  z-index: 9;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease;
}

/* Swiper */
.swiper-nav {
  position: absolute;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  pointer-events: none;
}
@media only screen and (max-width: 991px) {
  .swiper-nav {
    justify-content: center;
    gap: 20px;
  }
}
.swiper-nav .swiper-btn-prev,
.swiper-nav .swiper-btn-next {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 37px;
  background-color: #ff9c34;
  border-radius: 10px 6px 10px 6px;
  transform: skewX(-8deg);
  cursor: pointer;
  pointer-events: auto;
  transition: background-color 0.3s ease;
}
.swiper-nav .swiper-btn-prev:hover,
.swiper-nav .swiper-btn-next:hover {
  background-color: rgba(255, 156, 52, 0.7);
}
.swiper-nav .swiper-btn-prev svg,
.swiper-nav .swiper-btn-next svg {
  transform: skewX(8deg);
}
.swiper-nav .swiper-button-disabled {
  filter: grayscale(100%);
  cursor: default;
}

/* Swiper pagination */
.swiper-pagination {
  display: none;
  align-items: center;
  justify-content: center;
  gap: 10px;
  position: absolute;
  top: unset !important;
  bottom: 16px !important;
  z-index: 12;
}
@media only screen and (max-width: 991px) {
  .swiper-pagination {
    display: flex;
  }
}
.swiper-pagination .swiper-pagination-bullet {
  width: 20px;
  height: 20px;
  opacity: 0.5;
  background-color: #ffffff;
  margin: 0 !important;
}
.swiper-pagination .swiper-pagination-bullet-active {
  opacity: 1;
}

/* Blocks */
.container {
  max-width: 1202px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
}
@media only screen and (max-width: 575px) {
  .container {
    padding: 0 15px;
  }
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  max-width: 155px;
  width: 100%;
  padding: 10px 20px;
  border-radius: 10px;
  box-sizing: border-box;
  background-color: #ff9c34;
  color: #ffffff;
  font-size: 14px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  transition: 0.3s ease;
}
.button:hover {
  background-color: rgba(255, 156, 52, 0.7);
}

.button-up {
  position: fixed;
  left: 88.7%;
  bottom: 35px;
  z-index: 12;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 35px;
  padding: 5px;
  border-radius: 3px;
  background-color: rgba(0, 0, 0, 0.2);
  opacity: 0;
  pointer-events: none;
  transition: background-color 0.3s ease;
  cursor: pointer;
}
@media only screen and (max-width: 991px) {
  .button-up {
    bottom: 15px;
  }
}
.button-up--visible {
  opacity: 1;
  pointer-events: auto;
}
.button-up:hover {
  background-color: rgba(0, 0, 0, 0.7);
}

.title {
  font-weight: 600;
  font-size: 40px;
  line-height: 1.2;
}
@media only screen and (max-width: 1279px) {
  .title {
    font-size: calc(35px + (40 - 35) * (100vw - 992px) / (1280 - 992));
  }
}
@media only screen and (max-width: 991px) {
  .title {
    font-size: calc(28px + (35 - 28) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  .title {
    font-size: calc(20px + (28 - 20) * (100vw - 0px) / (768 - 0));
  }
}

.text {
  max-width: 800px;
}

.about-card {
  background-color: #f4f4f4;
  border-radius: 10px;
  padding: 38px 30px;
}
.about-card--dark {
  display: flex;
  flex-direction: column;
  gap: 33px;
  background-color: #070c3a;
  color: #ffffff;
}
.about-card__num {
  font-size: 50px;
}
.about-card__text {
  font-size: 18px;
}
.about-card__text p + p {
  margin-top: 23px;
}
.about-card__title {
  display: flex;
  align-items: center;
  gap: 31px;
  margin-bottom: 44px;
}
.about-card__title--column {
  flex-direction: column;
  align-items: flex-start;
  gap: 21px;
  margin-bottom: 35px;
}
.about-card__title span {
  font-size: 24px;
  font-weight: 500;
}

.header {
  position: relative;
  padding: 53px 0 0;
  background-color: #070c3a;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
.header__right {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  width: 45%;
}
.header__logo {
  transition: opacity 0.3s ease;
}
.header__logo:hover {
  opacity: 0.7;
}

.footer {
  position: relative;
  z-index: 10;
  padding: 70px 0 40px;
}

.article__home {
  color: #ffffff !important;
  margin-top: 40px;
}
@media only screen and (max-width: 767px) {
  .article__home {
    margin-top: 20px;
  }
}

.hero {
  padding: 84px 0 37px;
  background-color: #070c3a;
  color: #ffffff;
  border-radius: 0 0 10px 10px;
}
.hero__container {
  display: flex;
}
.hero__content {
  max-width: 59.7%;
}
.hero__title-big {
  font-size: 45px;
  line-height: 1.2;
}
@media only screen and (max-width: 1279px) {
  .hero__title-big {
    font-size: calc(38px + (45 - 38) * (100vw - 992px) / (1280 - 992));
  }
}
@media only screen and (max-width: 991px) {
  .hero__title-big {
    font-size: calc(32px + (38 - 32) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  .hero__title-big {
    font-size: calc(24px + (32 - 24) * (100vw - 0px) / (768 - 0));
  }
}
.hero__title-accent {
  color: #00b2ff;
}
.hero__text {
  margin-top: 40px;
}
.hero__text p + p {
  margin-top: 25px;
}
.hero__image-wrapper {
  position: relative;
  margin-top: -55px;
  margin-right: -85px;
  margin-bottom: -55px;
}
.hero__image-wrapper::before, .hero__image-wrapper::after {
  content: "";
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0.8);
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.12);
}
.hero__image-wrapper::before {
  animation: pulse-small 2.5s linear infinite;
  animation-delay: 2s;
  z-index: 1;
}
.hero__image-wrapper::after {
  animation: pulse-big 2.5s linear infinite;
  z-index: 0;
}
.hero__image-wrapper picture {
  position: relative;
  z-index: 2;
}

.about {
  padding: 44px 0 0;
}
.about__text {
  margin-top: 25px;
}
.about__cards {
  display: grid;
  grid-template-columns: 1.2fr 2fr 1.2fr;
  gap: 30px;
  margin-top: 30px;
}